@import "../shared/colors.scss";
@import "../shared/mixins.scss";
@import "../shared/vars.scss";

.editor {
  @media (max-width: $mobile-width-only) {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    padding-top: 20px;
    padding-bottom: 41px;
  }

  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 40px;
    padding-top: 57px;
    padding-bottom: 54px;
  }

  @media (min-width: $desktop-width) {
    grid-template-columns: 1fr 1fr 1fr;
  }

  &__fieldset {
    border: none;
    margin: 0;
    padding: 0;
    // outline: 2px solid rebeccapurple;
  }

  &__photo {
    @media (max-width: $mobile-width-only) {
      margin-bottom: 22px;
    }

    @media (min-width: $desktop-width) {
      grid-row: 1;
      grid-column: 1/3;
    }
  }

  &__settings {
    @media (max-width: $mobile-width-only) {
      align-self: center;
    }

    @media (min-width: $tablet-width) {
      display: none;
    }
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

    @media (min-width: $desktop-width) {
      grid-row: 1;
      grid-column: 2/4;
      margin-left: 150px;
    }
  }

  &__filters {
    @media (max-width: $mobile-width-only) {
      // display: none;
    }

    @media (min-width: $tablet-width) {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }
  }

  &__toggles {
    display: flex;
    justify-content: center;

    @media (max-width: $mobile-width-only) {
      margin-bottom: 22px;
    }
  }

  &__toggle {
    margin: 0;
    padding: 0;
    outline: none;
    border: none;
    background-color: transparent;

    path {
      fill: rgba($maincolor, 0.3);
    }

    &:hover path {
      fill: rgba($maincolor, 0.5);
    }

    &--active path {
      fill: $pinkcolor;
    }
  }

  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  &__item {
    display: flex;
    justify-content: space-between;
    padding-top: 13px;
    padding-bottom: 13px;

    @media (max-width: $mobile-width-only) {
      display: none;
    }
  }

  &__item--active {
    @media (max-width: $mobile-width-only) {
      display: block;
    }
  }

  &__item:not(:last-child) {
    @media (min-width: $tablet-width) {
      border-bottom: 1px solid rgba($maincolor, 0.1);
    }
  }

  &__icon {
    @media (max-width: $mobile-width-only) {
      display: none;
    }

    &--crop {
      @media (min-width: $tablet-width) {
        width: 22px;
        height: 22px;
      }

      @media (min-width: $desktop-width) {
        width: 44px;
        height: 44px;
      }
    }

    &--color {
      @media (min-width: $tablet-width) {
        width: 24px;
        height: 22px;
      }

      @media (min-width: $desktop-width) {
        width: 48px;
        height: 43px;
      }
    }

    &--balance {
      @media (min-width: $tablet-width) {
        width: 16px;
        height: 16px;
      }

      @media (min-width: $desktop-width) {
        width: 32px;
        height: 32px;
      }
    }
  }

  &__range {
    @media (max-width: $mobile-width-only) {
      width: 240px;
    }

    @media (min-width: $tablet-width) {
      width: 202px;
    }

    @media (min-width: $desktop-width) {
      width: 288px;
    }
  }

  &__buttons {
    width: 100%;

    @media (max-width: $mobile-width-only) {
      border-top: 1px solid rgba($maincolor, 0.1);
      padding-top: 30px;
    }

    @media (min-width: $desktop-width) {
      display: flex;
      justify-content: space-between;
    }
  }

  &__submit {
    width: 100%;

    @media (max-width: $mobile-width-only) {
      margin-bottom: 12px;
    }

    @media (min-width: $tablet-width) {
      // margin-right: 20px;
      margin-bottom: 12px;
    }

    @media (min-width: $desktop-width) {
      margin-bottom: 0;
      margin-right: 20px;
    }
  }

  &__reset {
    width: 100%;
  }
}
